import { useState, useEffect } from 'react'
import { Divider, Modal, Switch } from 'antd';
import {
    Form,
    Input,
    Button,
    Select,
    DatePicker,
    InputNumber,
} from 'antd';
import { requestToken } from 'src/api/axios';
import { Alert } from 'src/common/components/Alert';
import moment from 'moment';

export default function EditStaff({ open, setOpen }: any) {

    const handleCancel = () => {
        setOpen(0)
    };

    // console.log("open", open);

    const [staff, setStaff] = useState<any>();

    useEffect(() => {
        if (open) {
            requestToken({ method: "GET", url: `/staff?id=${open}` }).then(res => {
                setStaff(res.data.data);
                //console.log("res", res.data);
            }).catch(err => { })
        }
    }, [open])

    return (
        <div>

            <Modal
                onCancel={handleCancel}
                visible={open ? true : false}
                title="Chnh sa nhn vin"
                width={700}
                footer={null}
            >
                {staff && <Form
                    labelCol={{ span: 6 }}
                    wrapperCol={{ span: 14 }}
                    layout="horizontal"
                    size={"middle"}
                    onFinish={(data: any) => {
                        console.log("data", data);
                        requestToken({
                            method: "PUT", url: "/staff", data: {
                                ...data,
                                birthday: data.birthday.format("DD/MM/YYYY"),
                                id: staff.id
                            }
                        }).then((res) => {
                            if (res.data.status === "CREATED") {
                                Alert({ name: res.data.message, icon: "success" })
                            } else {
                                Alert({ name: res.data.message, icon: "info" })
                            }
                        }).catch((err) => {
                            Alert({ name: err.data.message, icon: "error" })
                        })
                    }}
                    initialValues={{
                        "address": staff?.address ?? "",
                        "birthday": moment(staff?.birthday),
                        "card_id": staff?.card_id ?? "",
                        "expYear": staff?.expYear ?? 0,
                        "name": staff?.name ?? "",
                        "position": staff?.position ?? "",
                        "rate": staff?.rate ?? "",
                        "username": staff?.user?.username ?? "",
                        "status": staff?.status
                    }}
                >

                    <Form.Item label="Ti khon" name="username">
                        <Input disabled value={staff.username} />
                    </Form.Item>

                    <Form.Item label="H v tn" name="name">
                        <Input />
                    </Form.Item>
                    <Form.Item label="CMT" name="card_id">
                        <Input />
                    </Form.Item>
                    <Form.Item label="Ngy sinh" name="birthday">
                        <DatePicker />
                    </Form.Item>

                    <Form.Item label="S nm kinh nghim" name="expYear">
                        <InputNumber />
                    </Form.Item>

                    <Form.Item label="Bc ngh" name="rate">
                        <Select>
                            <Select.Option value="low">Thp</Select.Option>
                            <Select.Option value="mid">Trung bnh</Select.Option>
                            <Select.Option value="high">Cao</Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item label="V tr cng vic" name="position">
                        <Input />
                    </Form.Item>
                    <Form.Item label="a ch" name="address">
                        <Input />
                    </Form.Item>
                    <Divider />
                    <Form.Item label="Trng thi" valuePropName="status" name="status">
                        <Switch defaultChecked={staff.status} />
                    </Form.Item>
                    <Divider />

                    <Button key="back" onClick={handleCancel}>
                        Hu
                    </Button>
                    <Button key="submit" type="primary" htmlType="submit">
                        Cp nht
                    </Button>
                </Form>}
            </Modal>
        </div>
    )
}

