import { Button, Table } from "antd";
import React, { useContext, useEffect, useState } from "react";
import { FaChevronLeft } from "react-icons/fa";
import { requestToken } from "src/api/axios";
import { CustomerContext } from "src/common/context/CustomerContext";
import { ToggleSidebarContext } from "src/common/context/ToggleSidebarContext";
import { ThemeContext } from "styled-components";
import AddPackage from "./AddPackage";
import RechargePackage from "./RechargePackage";
import ModalCancelPackage from "./ModalCancelPackage";
import ModalTakeInterest from "./ModalTakeInterest";
import { SInnerSidebar } from "./styles";

interface ITable {
  key: any;
  code: any;
  balance: any;
  saving: any;
}

export default function Accountmanagement() {
  const { theme } = useContext(ThemeContext);
  const { toggleSidebar } = useContext(ToggleSidebarContext);
  const { customer, setCustomer } = useContext(CustomerContext);
  const [dataSource, setDataSource] = useState<ITable[]>([]);
  const [reload, setreload] = useState(true);

  const musReload = () => {
    setreload(!reload);
  }
  const [modalnapPackage, setModalnap] = useState(false);

  // modal
  const [currentAccount, setCurrentAccount] = useState();
  const [modalDkPackage, setModalDK] = useState(false);
  const [modalCancelPackage, setModalCancelPackage] = useState(false);
  const [modalTakeInterest, setModalTakeInterest] = useState(false);

  const columns = [
    {
      title: "S ti khon",
      dataIndex: "code",
      key: "code",
    },
    {
      title: "S d",
      dataIndex: "balance",
      key: "balance",
    },
    {
      title: "Gi ti khon",
      dataIndex: "package",
      key: "package",
      render: (text: string, record: any) => {
        //console.log("record", record);
        return record?.apackage ? `${record?.apackage?.name} - ${record?.apackage?.apr}%/nm` : "Cha ng k gi ti khon."
      }
    },
    {
      title: "S d tit kim",
      dataIndex: "saving",
      key: "saving",
    },
    {
      title: "Tin li tit kim",
      dataIndex: "balance_interest",
      key: "balance_interest",
      render: (text: string, record: any) => {
        //console.log("record", record);
        return record?.balance_interest ?? "0";
      },
    },
    {
      title: "Chc nng",
      dataIndex: "handle",
      key: "hanlde",
      render: (text: string, record: any) => {
        return (
          <>
            {record.apackage ? (
              <>
                <Button
                  onClick={() => {
                    setCurrentAccount(record);
                    setModalCancelPackage(true);
                  }}
                >
                  Hu gi{" "}
                </Button>
                <Button
                  onClick={() => {
                    setCurrentAccount(record);
                    setModalTakeInterest(true);
                  }}
                >
                  Rt li{" "}
                </Button>
                <Button onClick={() => {
                  setCurrentAccount(record);
                  setModalnap(true);
                }}>Np thm tin tit kim </Button>
              </>
            ) : (
              <Button
                onClick={() => {
                  setCurrentAccount(record);
                  setModalDK(true);
                }}
              >
                ng k gi{" "}
              </Button>
            )}
          </>
        );
      },
    },
  ];
  useEffect(() => {
    requestToken({
      method: "GET",
      url: "/auth/profile",
      // data: values,
    })
      .then((res: any) => {
        setCustomer(res.data.data);
        requestToken({
          method: "GET",
          url: "/customer/account",
          params: { customerId: res.data.data.id },
        }).then((res: any) => {
          if (res.data.data.content.length > 0) {
            const tempList: ITable[] = [];
            res.data.data.content.map((item: any, key: any) => {
              let rowItem = {
                ...item,
                key: key,
                code: item.code,
                balance: item.balance,
                saving: "Khng c",
              };
              if (item.apackage !== null) {
                rowItem = {
                  ...rowItem,
                  saving: item.balance_saving + "",
                };
              }

              tempList.push(rowItem);
            });
            setDataSource(tempList);
          }
        });
      })
      .catch((err: any) => {
        console.log("err: ", err);
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reload]);

  return (
    <SInnerSidebar>
      <div className="top">
        <h3
          className="header"
          onClick={() => toggleSidebar && toggleSidebar(false)}
        >
          <FaChevronLeft color={theme.text.main} size={16} />
          Qun l ti khon
        </h3>
      </div>
      <div className="table-container">
        <Table dataSource={dataSource} columns={columns} />
      </div>

      {
        modalnapPackage &&
        <RechargePackage
          data={currentAccount}
          open={modalnapPackage}
          setOpen={setModalnap} callback={() => {
            musReload()
          }} />
      }
      {modalDkPackage && (
        <AddPackage
          data={currentAccount}
          open={modalDkPackage}
          setOpen={setModalDK}
          callback={() => { musReload() }}
        />
      )}
      {modalCancelPackage && (
        <ModalCancelPackage
          currentAccount={currentAccount}
          open={modalCancelPackage}
          setOpen={setModalCancelPackage}
          callback={() => { musReload() }}
        />
      )}
      {modalTakeInterest && (
        <ModalTakeInterest
          currentAccount={currentAccount}
          open={modalTakeInterest}
          setOpen={setModalTakeInterest}
          callback={() => { musReload() }}
        />
      )}
    </SInnerSidebar>
  );
}
