import { Modal } from 'antd';
import {
    Form,
    Input,
    Button,
} from 'antd';
import { requestToken } from 'src/api/axios';
import { Alert } from 'src/common/components/Alert';

export default function CreatePackage({ open, setOpen, callback }: any) {

    const handleCancel = () => {
        setOpen(false)
    };

    return (
        <div>
            <Modal
                onCancel={handleCancel}
                visible={open}
                title="Thm mi Gi ti khon"
                width={700}
                footer={null}
            >
                <Form
                    labelCol={{ span: 6 }}
                    wrapperCol={{ span: 14 }}
                    layout="horizontal"
                    size={"middle"}
                    onFinish={(data: any) => {
                        //console.log("data", data);
                        requestToken({
                            method: "POST", url: "/package", data: {
                                ...data,
                                type: "saving"
                            }
                        }).then((res) => {
                            if (res.data.status === "CREATED") {
                                Alert({ name: res.data.message, icon: "success" })
                            } else {
                                Alert({ name: res.data.message, icon: "info" })
                            }
                            callback();
                        }).catch((err) => {
                            Alert({ name: err?.data?.message ?? "C li xy ra.", icon: "error" })
                        })
                    }}
                >

                    <Form.Item label="Tn gi" name="name">
                        <Input />
                    </Form.Item>
                    <Form.Item label="Li sut" name="apr">
                        <Input />
                    </Form.Item>
                    <Form.Item label="S d ti thiu" name="minBalance">
                        <Input />
                    </Form.Item>
                    <Button key="back" onClick={handleCancel}>
                        Hu
                    </Button>
                    <Button key="submit" type="primary" htmlType="submit">
                        To
                    </Button>
                </Form>
            </Modal>
        </div>
    )
}

