import { Modal } from 'antd';
import {
    Form,
    Input,
    Button,
    Select,
    DatePicker,
    InputNumber,
} from 'antd';
import { requestToken } from 'src/api/axios';
import { Alert } from 'src/common/components/Alert';

export default function CreateStaff({ open, setOpen, callback }: any) {

    const handleCancel = () => {
        setOpen(false)
    };

    return (
        <div>
            <Modal
                onCancel={handleCancel}
                visible={open}
                title="Thm mi nhn vin"
                width={700}
                footer={null}
            >
                <Form
                    labelCol={{ span: 6 }}
                    wrapperCol={{ span: 14 }}
                    layout="horizontal"
                    size={"middle"}
                    onFinish={(data: any) => {
                        //console.log("data", data);
                        requestToken({
                            method: "POST", url: "/staff", data: {
                                ...data,
                                birthday: data.birthday.format("DD/MM/YYYY")
                            }
                        }).then((res) => {
                            if (res.data.status === "CREATED") {
                                Alert({ name: res.data.message, icon: "success" })
                            } else {
                                Alert({ name: res.data.message, icon: "info" })
                            }
                            callback();
                        }).catch((err) => {
                            Alert({ name: err?.data?.message??"C li xy ra.", icon: "error" })
                        })
                    }}
                >

                    <Form.Item label="Ti khon" name="username">
                        <Input />
                    </Form.Item>
                    <Form.Item label="Mt khu" name="password">
                        <Input />
                    </Form.Item>
                    <Form.Item label="H v tn" name="name">
                        <Input />
                    </Form.Item>
                    <Form.Item label="CMT" name="card_id">
                        <Input />
                    </Form.Item>
                    <Form.Item label="Ngy sinh" name="birthday">
                        <DatePicker />
                    </Form.Item>

                    <Form.Item label="S nm kinh nghim" name="exp_year">
                        <InputNumber />
                    </Form.Item>

                    <Form.Item label="Bc ngh" name="rate">
                        <Select>
                            <Select.Option value="low">Thp</Select.Option>
                            <Select.Option value="mid">Trung bnh</Select.Option>
                            <Select.Option value="high">Cao</Select.Option>
                        </Select>
                    </Form.Item>

                    <Form.Item label="V tr cng vic" name="position">
                        <Input />
                    </Form.Item>
                    <Form.Item label="a ch" name="address">
                        <Input />
                    </Form.Item>

                    <Button key="back" onClick={handleCancel}>
                        Hu
                    </Button>
                    <Button key="submit" type="primary" htmlType="submit">
                        To
                    </Button>
                </Form>
            </Modal>
        </div>
    )
}

